/*---------------- 1.1 Preloader Starts ------------------*/
#loading {
    height: 100%;
    width: 100%;
    position: fixed;
    z-index: 2000;
    top: 0;
    background: #fff;
}

.loader {
    position: absolute;
    top: 50%;
    left: 40%;
    margin-left: 10%;
    transform: translate3d(-50%, -50%, 0);
}

.loader .dot {
    width: 24px;
    height: 24px;
    background: #79d1d6;
    border-radius: 100%;
    display: inline-block;
    animation: slide 1s infinite;
}

.loader .dot:nth-child(1) {
    animation-delay: 0.1s;
    background: #79d1d6;
}

.loader .dot:nth-child(2) {
    animation-delay: 0.2s;
    background: #5ec6ce;
}

.loader .dot:nth-child(3) {
    animation-delay: 0.3s;
    background: #90d3a6;
}

.loader .dot:nth-child(4) {
    animation-delay: 0.4s;
    background: #b8c800;
}

.loader .dot:nth-child(5) {
    animation-delay: 0.5s;
    background: #7fba00;
}

@-moz-keyframes slide {
    0% {
        transform: scale(1);
    }

    50% {
        opacity: 0.3;
        transform: scale(2);
    }

    100% {
        transform: scale(1);
    }
}

@-webkit-keyframes slide {
    0% {
        transform: scale(1);
    }

    50% {
        opacity: 0.3;
        transform: scale(2);
    }

    100% {
        transform: scale(1);
    }
}

@-o-keyframes slide {
    0% {
        transform: scale(1);
    }

    50% {
        opacity: 0.3;
        transform: scale(2);
    }

    100% {
        transform: scale(1);
    }
}

@keyframes slide {
    0% {
        transform: scale(1);
    }

    50% {
        opacity: 0.3;
        transform: scale(2);
    }

    100% {
        transform: scale(1);
    }
}

